@import 'ScssHelpers/helpers-all.scss';

.card_item {
	display: flex;
	align-items: stretch;
	flex-direction: column;

	width: 100%;
	height: 100%;

	border-radius: $gl_radius;
	overflow: hidden;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.card_item__pic {
	position: relative;
	display: block;
	width: 100%;

	overflow: hidden;

	&::after {
		content: '';
		display: block;

		@include bp($point_4, min) {
			padding-bottom: 83.4%;
		}

		@include bp($point_4 - 1) {
			padding-bottom: 89.8%;
		}
	}
}

.card_item__img {
	position: absolute;
	top: 0;
	left: 0;

	transition: transform 0.3s ease;

	@include cover_img;

	.card_item:hover &,
	.card_item:focus & {
		transform: scale(1.1);
	}
}

.card_item__content {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;

	text-align: center;
	background-color: $white_2;

	@include bp($point_4, min) {
		padding: 4rem 3rem 3.4rem;
	}

	@include bp($point_4 - 1) {
		padding: 2.2rem 2.4rem 2.8rem;
	}
}

.card_item__title {
	@include h4;

	color: $black_2;
	font-weight: 700;
	font-family: $font_2;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 1.2rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 0.6rem;
		}
	}
}

.card_item__text {
	@include text;
	color: $black_2;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 0.6rem;
		}
	}
}

.card_item__link {
	position: relative;
	align-self: start;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	margin: auto auto 0;

	text-align: center;
	color: $black_2;

	&::after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;

		width: 100%;
		height: 1px;

		background-color: $black;

		transition: opacity 0.3s ease;

		.card_item:hover &,
		.card_item:focus & {
			opacity: 1;
		}

		@include bp($point_2, min) {
			opacity: 0;
		}
	}

	@include bp($point_4, min) {
		font-size: 1.5rem;
		font-weight: 500;
		line-height: 133%;
		letter-spacing: 0.02em;
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;
		font-weight: 700;
		line-height: 140%;
	}
}
