@import 'ScssHelpers/helpers-all.scss';

.latest_news {
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 50%;

		background-color: $green;
		border-radius: 50%;

		transform: translateX(-50%);

		@include bp($point_4, min) {
			width: 63vw;
			height: 63vw;
		}

		@include bp($point_4 - 1) {
			width: 90vw;
			height: 90vw;
		}
	}

	&::before {
		content: '';
		position: absolute;

		left: 0;
		width: 100%;

		background-color: $green;

		@include bp($point_2, min) {
			top: 20.4vh;
			height: calc(100% - 20.4vh);
			border-radius: $gl_radius $gl_radius 0 0;
		}
		@include bp($point_3, $point_6) {
			top: 10.4vh;
			height: calc(100% - 10.4vh);
		}

		@include bp($point_6 - 1) {
			height: calc(100% - 8.8vh);
			top: 8.8vh;
		}
	}

	&:not(:last-child) {
		@include bp($point_1, min) {
			margin-bottom: 52rem;
			padding-bottom: 3.6rem;
		}

		@include bp($point_1 - 1, $point_2) {
			margin-bottom: 20rem;
		}
	}

	@include bp($point_0, min) {
		padding-top: 40rem;
	}

	@include bp($point_0 - 1, $point_2) {
		padding-top: 20.6rem;
	}

	@include bp($point_3) {
		padding-bottom: 0;
	}

	@include bp($point_3, $point_6) {
		padding-top: 14.6rem;
	}

	@include bp($point_6 - 1) {
		padding-top: 7.6rem;
	}
}

.latest_news__bg {
	position: absolute;
	z-index: 2;
	top: 0;
	left: 50%;

	height: 100%;

	@include bp($point_4, min) {
		width: 100%;
		transform: translateX(-50%);
	}

	@include bp($point_4 - 1) {
		width: 147%;
		transform: translateX(-50.3%);
	}
}

.latest_news__bg_img {
	object-position: top center;
	@include contain_img;
}

.latest_news__content {
	position: relative;
	z-index: 2;
}

.latest_news__title {
	margin: 0 auto;

	@include h2;
	color: $black_2;
	font-family: $font_2;
	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_6, min) {
		max-width: 60rem;
	}

	@include bp($point_6 - 1) {
		max-width: 30rem;
	}
}

.latest_news__slider {
	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 5.6rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2.8rem;
		}
	}
}

.latest_news__list {
	@include bp($point_2, min) {
		display: flex;
		align-items: stretch;

		flex-wrap: wrap;
		margin: 0 -2rem -2rem 0;
		width: auto;
	}

	@include bp($point_3) {
		margin: 0 -1.5rem -1.5rem 0;
	}
}

.latest_news__item {
	&.swiper-slide {
		height: auto;
	}

	@include bp($point_1, min) {
		width: 25%;
	}
	@include bp($point_1 - 1, $point_2) {
		width: 50%;
	}

	@include bp($point_2, min) {
		flex-shrink: 0;
		padding: 0 2rem 2rem 0;
	}

	@include bp($point_3) {
		padding: 0 1.5rem 1.5rem 0;
	}
}

.latest_news__link {
	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 12rem;
		}

		@include bp($point_3) {
			margin-bottom: 6.4rem;
		}
	}
}

.latest_news__banner {
	@include bp($point_1, min) {
		margin-bottom: -37.6rem;
	}

	@include bp($point_1 - 1, $point_2) {
		margin-bottom: -30rem;
	}
}
