@import 'ScssHelpers/helpers-all.scss';

.planning {
	&.planning--offset_mod {
		@include bp($point_2, min) {
			margin-top: -19.6rem;
		}
		@include bp($point_3) {
			margin-top: -30rem;
		}
	}

	z-index: 2;

	@include bp($point_3) {
		padding: 8rem var(--gl-indent) 2rem;
	}
}

.planning__heading {
	&:not(:last-child) {
		margin-bottom: 5.6rem;
	}
}

.planning__in {
	border: 2px solid $black_2;
	border-radius: 0.8rem;
	box-shadow: 1rem 1rem 1.5rem 0 rgba(0, 0, 0, 0.1);
	background-color: $white_2;

	@include bp($point_2, min) {
		padding: 4rem 4rem;
	}
	@include bp($point_3) {
		padding: 3.2rem 3rem 4rem;
	}
}

.planning__title {
	@include h2;

	max-width: 77.6rem;
	width: 100%;
	margin-left: auto;
	margin-right: auto;

	font-family: $font_2;
	text-align: center;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.planning__descr {
	max-width: 44rem;
	width: 100%;
	margin: 0 auto;

	font-size: 2rem;
	font-weight: 500;
	letter-spacing: 0.03em;
	line-height: 130%;
	text-align: center;

	p:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.planning__form_in {
	max-width: 129.2rem;
	width: 100%;
	margin: 0 auto;
}

.planning__form_row {
	margin: 0 -4rem -2.4rem 0;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
}

.planning__form_col {
	@include flex_block(row, wrap, flex-start, center, center);

	padding: 0 4rem 2.4rem 0;

	&:last-child {
		flex-shrink: 1;
	}
}

.planning__form_label {
	font-weight: 700;
	color: $black;
	letter-spacing: 0.2em;

	text-transform: uppercase;

	@include bp($point_2, min) {
		padding: 1rem 4rem 1rem 0rem;
		font-size: 1.6rem;
	}
	@include bp($point_3) {
		margin-bottom: 1.4rem;
		width: 100%;
		text-align: center;
		font-size: 1.4rem;
	}
}

.planning__form_select_w {
	position: relative;

	&:after {
		position: absolute;

		z-index: 1;

		content: '\25BC';
		font-size: 2rem;
		color: $black_2;
		pointer-events: none;
		transition: transform ease 0.3s;

		@include bp($point_2, min) {
			top: 3.4rem;
			right: 2.2rem;
		}
		@include bp($point_3) {
			top: 2.2rem;
			right: 2.4rem;
		}
	}

	// &:hover::after {
	// 	transform: rotate(180deg);
	// }

	@include bp($point_3) {
		width: 100%;
		max-width: 40rem;
		margin: 0 auto;
	}
}

.planning__form_select {
	color: $black_2;

	border: 2px solid $black_2;

	background-color: $white_2;
	border-radius: 0.8rem;

	appearance: none;
	-webkit-appearance: none;
	overflow: hidden;

	line-height: 1.2;
	font-weight: 700;
	font-family: $font_2;

	cursor: pointer;
	transition: background-color ease 0.3s;

	option {
		font-size: 1.8rem;
		line-height: 1;
		font-weight: 400;
		font-family: $font_1;
	}

	.planning__form_select--guests & {
		@include bp($point_2, min) {
			max-width: 29rem;
		}
	}

	// &:hover {
	// 	background-color: $white;
	// }

	@include bp($point_2, min) {
		min-width: 29rem;
		padding: 2.2rem 5rem 2.2rem 2.5rem;
		font-size: 3.2rem;
	}

	@include bp($point_3) {
		padding: 1.8rem 5rem 1.8rem 2.5rem;
		width: 100%;
		font-size: 2.4rem;
	}
}

.planning__form_btn {
	@include bp($point_3) {
		margin: 0 auto;
		width: 100%;
		max-width: 40rem;

		color: $white_2;
		background-color: $black_2;
		border: 1px solid $white_2;

		&:hover {
			background-color: rgba($black_2, 0);
			color: $black_2;
			border-color: $black_2;
		}
	}
}
